@media screen and (max-width: (map-get($map: $screen-sizes, $key: "xs") - 1px)) {
  #suppliers-container {
    grid-template-columns: 1fr;

    .supplier.expanded {
      grid-column: span 1;
      grid-row: span 2;
    }
  }

  .supplier-content-main .supplier-product-carousel-container {
    max-height: 100px !important;
  }
}
